import React from "react";

const letterStyle = {
    first: {
        fontFamily: "FrederickatheGreat-Regular", 
        fontSize: "xx-large"
    }
}

function Chapter(props) {
    const {books, book, chapter, verses} = props;

    return(
        <div className="chapter">
            <h3>{books.filter(item => (item.eng_short === book))[0].name} {chapter}.</h3>
            <hr></hr>
            {verses.map(verse => (
            verse.verse_no === 1 ?
                    <span key={verse._id}>
                        &nbsp; &nbsp;
                        <sup>
                            {verse.verse_no}
                        </sup>
                        <span style={ letterStyle.first }>{verse.verse_text.charAt(0)}</span>{verse.verse_text.slice(1)}
                    </span> :
                    <span key={verse._id}>
                        &nbsp;
                        <sup>
                            {verse.verse_no}
                        </sup>
                        {verse.verse_text}
                    </span>
                )
            )}
        </div>
    );
}

export default Chapter;